<template>
    <div style="width: 100%">
        <div style="margin-top: 10px">
            <el-table
                    ref="multipleTable"
                    stripe
                    border
                    max-height="513"
                    :data="appatts"
                    v-loading="appattLoading"
                    element-loading-text="..."
                    element-loading-spinner="el-icon-loading"
                    element-loading-background="rgba(0, 0, 0, 0.8)"
                    @selection-change="handleSelectionChange">
                <el-table-column
                        align="center"
                        type="selection"
                        width="55">
                </el-table-column>
                <el-table-column
                        header-align="center"
                        fixed
                        prop="aatid"
                        label=""
                        align="left"
                        width="85">
                </el-table-column>
                <el-table-column
                        header-align="center"
                        prop="employee.empname"
                        fixed
                        align="left"
                        label=""
                        width="90">
                </el-table-column>
                <el-table-column
                        header-align="center"
                        prop="workid"
                        label=""
                        align="left"
                        width="85">
                </el-table-column>
                <el-table-column
                        header-align="center"
                        prop="department.depname"
                        width="100"
                        align="left"
                        label="">
                </el-table-column>
                <el-table-column
                        header-align="center"
                        prop="department.leadername"
                        width="100"
                        align="left"
                        label="">
                </el-table-column>
                <el-table-column
                        header-align="center"
                        prop="category"
                        width="100"
                        align="left"
                        label="">
                </el-table-column>
                <el-table-column
                        show-overflow-tooltip
                        header-align="center"
                        prop="reason"
                        width="300"
                        align="left"
                        label="">
                </el-table-column>
                <el-table-column
                        header-align="center"
                        prop="appbegindate"
                        width="140"
                        align="left"
                        label="">
                </el-table-column>
                <el-table-column
                        header-align="center"
                        prop="appenddate"
                        width="140"
                        align="left"
                        label="">
                </el-table-column>
                <el-table-column
                        header-align="center"
                        prop="total"
                        width="100"
                        align="left"
                        label="">
                </el-table-column>
                <el-table-column
                        header-align="center"
                        prop="appdate"
                        width="140"
                        align="left"
                        label="">
                </el-table-column>
                <el-table-column
                        width="100"
                        align="center"
                        label="">
                    <template slot-scope="scope">
                        <el-tag>{{isSubmit(scope.row.issubmit)}}</el-tag>
                    </template>
                </el-table-column>
                <el-table-column
                        width="100"
                        align="center"
                        label="">
                    <template slot-scope="scope">
                        <el-tag type="success">{{isApprove(scope.row.isapprove)}}</el-tag>
                    </template>
                </el-table-column>
                <el-table-column
                        align="center"
                        fixed="right"
                        width="80px"
                        label="">
                    <template slot-scope="scope">
                        <el-button type="text" @click="checkAppatt(scope.row)" style="color: #15aabf"><i class="fas fa-eye fa-lg"/></el-button>
                        <el-button type="text" @click="deleteAppatt(scope.row)" style="color: #e30007"><i class="fas fa-trash-alt fa-lg"/></el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>
        <div style="display: flex;justify-content: space-between">
            <el-button type="danger" icon="el-icon-delete" @click="batchDeleteAppatt"></el-button>
            <el-pagination
                    background
                    @current-change="currentChange"
                    @size-change="sizeChange"
                    layout="sizes, prev, pager, next, jumper, ->, total, slot"
                    :total="total">
            </el-pagination>
        </div>
        <el-dialog
                title=""
                :visible.sync="editAppattDialog"
                width="40%">
            <div>
                <el-form :model="appatt" status-icon :rules="rules" ref="appattForm" disabled>
                    <el-row>
                        <el-col :span="12">
                            <el-form-item label=":" prop="workid">
                                <el-input disabled size="mini" style="width: 100%" prefix-icon="el-icon-edit" v-model="appatt.workid" placeholder=""></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label=":" prop="category">
                                <el-select clearable v-model="appatt.category" placeholder="" size="mini"
                                           style="width: 100%">
                                    <el-option
                                            v-for="item in category"
                                            :key="item"
                                            :label="item"
                                            :value="item">
                                    </el-option>
                                </el-select>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="24">
                            <el-form-item label=":" prop="reason">
                                <el-input
                                        clearable
                                        style="width: 100%"
                                        type="textarea"
                                        :autosize="{ minRows: 2, maxRows: 7}"
                                        placeholder=""
                                        v-model="appatt.reason"
                                        minlength="10"
                                        maxlength="255"
                                        show-word-limit>
                                </el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="12">
                            <el-form-item label=":" prop="appbegindate">
                                <el-date-picker
                                        v-model="appatt.appbegindate"
                                        size="mini"
                                        type="datetime"
                                        value-format="yyyy-MM-dd HH:mm:ss"
                                        placeholder=""
                                        style="width: 100%;"
                                        default-time="09:00:00">
                                </el-date-picker>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label=":" prop="appenddate">
                                <el-date-picker
                                        v-model="appatt.appenddate"
                                        size="mini"
                                        type="datetime"
                                        value-format="yyyy-MM-dd HH:mm:ss"
                                        placeholder=""
                                        style="width: 100%;"
                                        default-time="09:00:00">
                                </el-date-picker>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="12">
                            <el-form-item label=":" prop="total">
                                <el-input disabled size="mini" style="width: 100%" prefix-icon="el-icon-edit" v-model.number="appatt.total" placeholder=""></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="12">
                            <el-form-item label=":" prop="appdate">
                                <el-date-picker
                                        v-model="appatt.appdate"
                                        size="mini"
                                        type="datetime"
                                        value-format="yyyy-MM-dd HH:mm:ss"
                                        placeholder=""
                                        style="width: 100%;"
                                        default-time="09:00:00">
                                </el-date-picker>
                            </el-form-item>
                        </el-col>
                    </el-row>
                </el-form>
            </div>
            <div slot="footer" class="dialog-footer">
                <el-button type="primary" @click="editAppattDialog = false"> </el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
    export default {
        name: "InhaApp",
        data(){
            return{
                page: 1,
                size: 10,
                total: 0,
                appatts: [],
                appattLoading: false,
                editAppattDialog: false,
                multipleSelection: [],
                category: ['','','',''],
                appatt: {
                    workid: 1,
                    category: '',
                    reason: '',
                    appbegindate: '2019-08-05 09:00:00',
                    appenddate: '2019-08-05 18:00:00',
                    total: 8,
                    appdate: '2019-08-05 07:00:00',
                    issubmit: false
                },
                rules: {
                    workid: [{required: true, message: '', trigger: 'blur'}],
                    category: [{required: true, message: '', trigger: 'change'}],
                    reason: [{required: true, message: '', trigger: 'blur'}],
                    appbegindate: [{required: true, message: '', trigger: 'blur'}],
                    appenddate: [{required: true, message: '', trigger: 'blur'}],
                    total: [{required: true, message: '', trigger: 'blur'},{
                        type: 'number',
                        message: '',
                        trigger: 'blur'
                    }],
                    appdate: [{required: true, message: '', trigger: 'blur'}],
                }
            }
        },
        mounted() {
            this.initAppatts();
        },
        methods: {
            initAppatts(){
                this.appattLoading=true;
                this.getRequest('/attendance/transactionapp/inhandappatt/?page=' + this.page + '&size=' + this.size).then(resp => {
                    this.appattLoading = false;
                    if (resp) {
                        this.appatts = resp.data;
                        this.total = resp.total;
                        if(resp.data.length===0){
                            this.$notify.error({
                                title: '',
                                message: ''
                            });
                        }
                    }
                });
            },
            sizeChange(currentSize) {
                this.size = currentSize;
                this.initAppatts();
            },
            currentChange(currentPage) {
                this.page = currentPage;
                this.initAppatts();
            },
            checkAppatt(data){
                this.appatt=Object.assign({}, data);
                this.editAppattDialog=true;
            },
            deleteAppatt(data){
                this.$confirm('' + data.aatid + ', ?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    this.deleteRequest("/attendance/transactionapp/" + data.aatid).then(resp => {
                        if (resp) {
                            this.initAppatts();
                        }
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: ''
                    });
                });
            },
            batchDeleteAppatt(){
                this.$confirm(', ?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    this.postRequest("/attendance/transactionapp/deleteappatts/",this.multipleSelection).then(resp => {
                        if (resp) {
                            this.initAppatts();
                        }
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: ''
                    });
                });
            },
            handleSelectionChange(val) {
                this.multipleSelection = val;
            },
            isSubmit(cellValue){
                if(cellValue===false)
                    return '';
                else
                    return '';
            },
            isApprove(cellValue){
                if(cellValue===true)
                    return '';
                else if(cellValue===false)
                    return '';
                else
                    return '';
            }
        }
    }
</script>

<style scoped>

</style>