<template>
    <div style="width: 100%">
        <div>
            <div class="top-menu">
                <div>
                    :
                    <el-popover
                            placement="right"
                            title=""
                            width="200"
                            trigger="manual"
                            v-model="searchDepView">
                        <el-tree default-expand-all :data="allDeps" :props="defaultProps"
                                 @node-click="searvhViewHandleNodeClick"></el-tree>
                        <div class="select-dep" slot="reference"
                             @click="showSearchDepView">{{inputDepName}}
                        </div>
                    </el-popover>
                    :
                    <el-date-picker
                            v-model="searchValue.caldate"
                            type="daterange"
                            unlink-panels
                            align="right"
                            value-format="yyyy-MM-dd"
                            start-placeholder=""
                            range-separator=""
                            end-placeholder=""
                            style="width: 340px">
                    </el-date-picker>
                    <el-input class="search-input" placeholder="..." prefix-icon="el-icon-search"
                              clearable
                              @clear="initAtteMon"
                              v-model="searchValue.empname"
                              @keydown.enter.native="initAtteMon">
                    </el-input>
                    <el-button icon="el-icon-search" type="primary" @click="initAtteMon">
                        
                    </el-button>
                </div>
                <div>
                    <el-button type="statistics" @click="statistics"><i style="margin-right: 8px" class="fas fa-calculator fa-lg"/></el-button>
                    <el-button type="success" icon="el-icon-download" @click="exportData">
                        
                    </el-button>
                </div>
            </div>
        </div>
        <div style="margin-top: 10px">
            <el-table
                    ref="multipleTable"
                    stripe
                    border
                    max-height="513"
                    :data="atteMons"
                    v-loading="atteMonLoading"
                    element-loading-text="..."
                    element-loading-spinner="el-icon-loading"
                    element-loading-background="rgba(0, 0, 0, 0.8)"
                    @selection-change="handleSelectionChange">
                <el-table-column
                        align="center"
                        type="selection"
                        width="55">
                </el-table-column>
                <el-table-column
                        header-align="center"
                        fixed
                        prop="attelogmonid"
                        label=""
                        align="left"
                        width="85">
                </el-table-column>
                <el-table-column
                        header-align="center"
                        prop="employee.empname"
                        fixed
                        align="left"
                        label=""
                        width="90">
                </el-table-column>
                <el-table-column
                        header-align="center"
                        prop="workid"
                        label=""
                        align="left"
                        width="85">
                </el-table-column>
                <el-table-column
                        header-align="center"
                        prop="department.depname"
                        width="100"
                        align="left"
                        label="">
                </el-table-column>
                <el-table-column
                        header-align="center"
                        prop="totalh"
                        width="100"
                        align="left"
                        label="">
                </el-table-column>
                <el-table-column
                        header-align="center"
                        prop="normalh"
                        width="100"
                        align="left"
                        label="">
                </el-table-column>
                <el-table-column
                        header-align="center"
                        prop="absenth"
                        width="100"
                        align="left"
                        label="">
                </el-table-column>
                <el-table-column
                        header-align="center"
                        prop="leaveh"
                        width="100"
                        align="left"
                        label="">
                </el-table-column>
                <el-table-column
                        header-align="center"
                        prop="vacateh"
                        width="100"
                        align="left"
                        label="">
                </el-table-column>
                <el-table-column
                        header-align="center"
                        prop="businessh"
                        width="100"
                        align="left"
                        label="">
                </el-table-column>
                <el-table-column
                        header-align="center"
                        prop="overtimeh"
                        width="100"
                        align="left"
                        label="">
                </el-table-column>
                <el-table-column
                        header-align="center"
                        prop="firstday"
                        width="145"
                        align="left"
                        label="">
                </el-table-column>
                <el-table-column
                        header-align="center"
                        prop="endday"
                        width="145"
                        align="left"
                        label="">
                </el-table-column>
                <el-table-column
                        header-align="center"
                        prop="caldate"
                        width="145"
                        align="left"
                        label="">
                </el-table-column>
                <el-table-column
                        align="center"
                        fixed="right"
                        width="100px"
                        label="">
                    <template slot-scope="scope">
                        <el-button type="text" @click="checkAtteMon(scope.row)" style="color: #15aabf"><i class="fas fa-eye fa-lg"/></el-button>
                        <el-button type="text" @click="editAtteMonView(scope.row)"><i class="fas fa-edit  fa-lg"/></el-button>
                        <el-button type="text" @click="deleteAtteMon(scope.row)" style="color: #e30007"><i class="fas fa-trash-alt fa-lg"/></el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>
        <div style="display: flex;justify-content: space-between">
            <el-button type="danger" icon="el-icon-delete" @click="batchDelete"></el-button>
            <el-pagination
                    background
                    @current-change="currentChange"
                    @size-change="sizeChange"
                    layout="sizes, prev, pager, next, jumper, ->, total, slot"
                    :total="total">
            </el-pagination>
        </div>
        <el-dialog
                :title="title"
                :visible.sync="atteMonDialog"
                width="60%">
            <div>
                <el-form :model="atteMon" status-icon :rules="rules" ref="atteMonForm" :disabled="NoModify" label-position="left" label-width="90px">
                    <el-row>
                        <el-col :span="8">
                            <el-form-item label=":" prop="workid">
                                <el-input size="mini" style="width: 90%" prefix-icon="el-icon-edit" v-model.number="atteMon.workid" placeholder=""></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8">
                            <el-form-item label=":" prop="totalh">
                                <el-input size="mini" style="width: 90%" prefix-icon="el-icon-edit" v-model.number="atteMon.totalh" placeholder=""></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8">
                            <el-form-item label=":" prop="normalh">
                                <el-input size="mini" style="width: 90%" prefix-icon="el-icon-edit" v-model.number="atteMon.normalh" placeholder=""></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="8">
                            <el-form-item label=":" prop="absenth">
                                <el-input size="mini" style="width: 90%" prefix-icon="el-icon-edit" v-model.number="atteMon.absenth" placeholder=""></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8">
                            <el-form-item label=":" prop="leaveh">
                                <el-input size="mini" style="width: 90%" prefix-icon="el-icon-edit" v-model.number="atteMon.leaveh" placeholder=""></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8">
                            <el-form-item label=":" prop="vacateh">
                                <el-input size="mini" style="width: 90%" prefix-icon="el-icon-edit" v-model.number="atteMon.vacateh" placeholder=""></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="8">
                            <el-form-item label=":" prop="businessh">
                                <el-input size="mini" style="width: 90%" prefix-icon="el-icon-edit" v-model.number="atteMon.businessh" placeholder=""></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8">
                            <el-form-item label=":" prop="overtimeh">
                                <el-input size="mini" style="width: 90%" prefix-icon="el-icon-edit" v-model.number="atteMon.overtimeh" placeholder=""></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8">
                            <el-form-item label=":" prop="firstday">
                                <el-date-picker
                                        v-model="atteMon.firstday"
                                        size="mini"
                                        type="date"
                                        value-format="yyyy-MM-dd"
                                        placeholder=""
                                        style="width: 90%">
                                </el-date-picker>
                            </el-form-item>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="8">
                            <el-form-item label=":" prop="endday">
                                <el-date-picker
                                        v-model="atteMon.endday"
                                        size="mini"
                                        type="date"
                                        value-format="yyyy-MM-dd"
                                        placeholder=""
                                        style="width: 90%">
                                </el-date-picker>
                            </el-form-item>
                        </el-col>
                        <el-col :span="8">
                            <el-form-item label=":" prop="caldate">
                                <el-date-picker
                                        v-model="atteMon.caldate"
                                        size="mini"
                                        type="datetime"
                                        value-format="yyyy-MM-dd HH:mm:ss"
                                        placeholder=""
                                        style="width: 90%"
                                        default-time="09:00:00">
                                </el-date-picker>
                            </el-form-item>
                        </el-col>
                    </el-row>
                </el-form>
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button @click="atteMonDialog = false"> </el-button>
                <el-button type="primary" @click="doAddAtteMon"> </el-button>
            </span>
        </el-dialog>
        <el-dialog
                title=""
                :visible.sync="statisticsDialog"
                width="50%">
            <div>
                <el-steps :active="activeItemIndex" finish-status="success" align-center>
                    <el-step title="" description="1~31"></el-step>
                    <el-step title="" description="()"></el-step>
                    <el-step title="" description=""></el-step>
                </el-steps>
                <div style="margin-top: 10px;display: flex;justify-content: center">
                    <div v-show="activeItemIndex==0">
                        <el-date-picker
                                v-model="statisticsValue.monthday"
                                type="daterange"
                                unlink-panels
                                value-format="yyyy-MM-dd"
                                range-separator=""
                                start-placeholder=""
                                end-placeholder=""
                                style="width: 300px">
                        </el-date-picker>
                    </div>
                    <div v-show="activeItemIndex==1">
                        <el-radio-group v-model="way">
                            <el-radio label="0"></el-radio>
                            <el-radio label="1"></el-radio>
                            <el-radio label="2"></el-radio>
                        </el-radio-group>
                    </div>
                    <div v-show="activeItemIndex==2">
                        <div v-show="way==1">
                            :
                            <el-popover
                                    placement="right"
                                    title=""
                                    width="200"
                                    trigger="manual"
                                    v-model="statisticsDepView">
                                <el-tree default-expand-all :data="allDeps" :props="defaultProps"
                                         @node-click="statisticsViewHandleNodeClick"></el-tree>
                                <div class="select-dep" slot="reference"
                                     @click="showStatisticsDepView">{{inputStatisticsDepName}}
                                </div>
                            </el-popover>
                        </div>
                        <div v-show="way==2">
                            <el-select v-model="selectedEmps" value-key="workid" multiple placeholder="" :popper-append-to-body="false" class="findAllEmp-autocomplete">
                                <el-option
                                        v-for="item in employees"
                                        :key="item.workid"
                                        :label="item.label"
                                        :value="item">
                                    <div class="empname">{{ item.label }}</div>
                                    <span class="workid">: {{ item.workid }}</span>
                                </el-option>
                            </el-select>
                        </div>
                    </div>
                </div>
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button @click="preStep">{{activeItemIndex==2?'':''}}</el-button>
                <el-button type="primary" @click="nextStep">{{activeItemIndex==2?'':''}}</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
    export default {
        name: "AttManaOfMon",
        data(){
            return{
                searchValue: {
                    empname: null,
                    depid: null,
                    caldate: null,
                },
                statisticsValue: {
                    monthday: null,
                    depid: null,
                },
                title: '',
                page: 1,
                size: 10,
                total: 0,
                way: '0',
                atteMonDialog: false,
                searchDepView: false,
                statisticsDepView: false,
                atteMonLoading: false,
                statisticsDialog: false,
                NoModify: false,
                activeItemIndex: 0,
                inputDepName: '',
                inputStatisticsDepName: '',
                atteMons: [],
                allDeps: [],
                employees: [],
                selectedEmps: [],
                multipleSelection: [],
                atteMon: {},
                defaultProps: {
                    children: 'children',
                    label: 'depname'
                },
                rules: {
                    workid: [{required: true, message: '', trigger: 'blur'}],
                    totalh: [{required: true, message: '', trigger: 'blur'}, {
                        type: 'number',
                        message: '',
                        trigger: 'blur'
                    }],
                    normalh: [{required: true, message: '', trigger: 'blur'}, {
                        type: 'number',
                        message: '',
                        trigger: 'blur'
                    }],
                    absenth: [{required: true, message: '', trigger: 'blur'}, {
                        type: 'number',
                        message: '',
                        trigger: 'blur'
                    }],
                    leaveh: [{required: true, message: '', trigger: 'blur'}, {
                        type: 'number',
                        message: '',
                        trigger: 'blur'
                    }],
                    vacateh: [{required: true, message: '', trigger: 'blur'}, {
                        type: 'number',
                        message: '',
                        trigger: 'blur'
                    }],
                    businessh: [{required: true, message: '', trigger: 'blur'}, {
                        type: 'number',
                        message: '',
                        trigger: 'blur'
                    }],
                    overtimeh: [{required: true, message: '', trigger: 'blur'}, {
                        type: 'number',
                        message: '',
                        trigger: 'blur'
                    }],
                    firstday: [{required: true, message: '', trigger: 'blur'}],
                    endday: [{required: true, message: '', trigger: 'blur'}],
                    caldate: [{required: true, message: '', trigger: 'blur'}],
                }
            }
        },
        mounted(){
            this.initData();
            this.initAtteMon();
        },
        methods:{
            initAtteMon(){
                this.atteMonLoading=true;
                let url="/attendance/managementofmonth/?page=" + this.page + "&size=" + this.size;
                if(this.searchValue.empname){
                    url+="&empname="+this.searchValue.empname;
                }
                if(this.searchValue.depid){
                    url+="&depid="+this.searchValue.depid;
                }
                if(this.searchValue.caldate){
                    url+="&caldate="+this.searchValue.caldate;
                }
                this.getRequest(url).then(resp => {
                    this.atteMonLoading = false;
                    if (resp) {
                        this.atteMons = resp.data;
                        this.total = resp.total;
                        if(resp.data.length==0){
                            this.$notify.error({
                                title: '',
                                message: ''
                            });
                        }
                    }
                });
            },
            initData() {
                if (!window.sessionStorage.getItem("deps")) {
                    this.getRequest('/employee/basic/info/deps').then(resp => {
                        if (resp) {
                            this.allDeps = resp;
                            window.sessionStorage.setItem("deps", JSON.stringify(resp));
                        }
                    })
                } else {
                    this.allDeps = JSON.parse(window.sessionStorage.getItem("deps"));
                }
            },
            checkAtteMon(data){
                this.title='';
                this.atteMon = data;
                this.NoModify=true;
                this.atteMonDialog = true;
            },
            editAtteMonView(data){
                this.title = '';
                this.atteMon = Object.assign({},data);
                this.NoModify=false;
                this.atteMonDialog=true;
                this.$refs['atteMonForm'].resetFields();
            },
            deleteAtteMon(data){
                this.$confirm('' + data.employee.empname + ', ?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    this.deleteRequest("/attendance/managementofmonth/" + data.attelogmonid).then(resp => {
                        if (resp) {
                            this.initAtteMon();
                        }
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: ''
                    });
                });
            },
            batchDelete(){
                this.$confirm(', ?', '', {
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(() => {
                    this.postRequest("/attendance/managementofmonth/deleteattemons/",this.multipleSelection).then(resp => {
                        if (resp) {
                            this.initAtteMon();
                        }
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: ''
                    });
                });
            },
            doAddAtteMon(){
                if(this.NoModify==true){
                    this.atteMonDialog=false;
                    return;
                }
                this.$refs['atteMonForm'].validate(valid => {
                    if (valid) {
                        this.putRequest("/attendance/managementofmonth/", this.atteMon).then(resp => {
                            if (resp) {
                                this.atteMonDialog=false;
                                this.initAtteMon();
                            }
                        });
                    }
                });
            },
            preStep() {
                if (this.activeItemIndex == 0) {
                    return;
                } else if (this.activeItemIndex == 2) {
                    //
                    this.statisticsDialog = false;
                    return;
                }
                this.activeItemIndex--;
            },
            nextStep() {
                if (this.activeItemIndex == 2) {
                    if(this.way==0){
                        this.getRequest("/attendance/managementofmonth/calcall?monthday=" + this.statisticsValue.monthday).then(resp => {
                            if (resp) {
                                this.initAtteMon();
                                this.statisticsDialog = false;
                            }
                        });
                    }else if(this.way==1){
                        this.getRequest("/attendance/managementofmonth/calcbydepid?depid=" + this.statisticsValue.depid + "&monthday=" + this.statisticsValue.monthday).then(resp => {
                            if (resp) {
                                this.initAtteMon();
                                this.statisticsDialog = false;
                            }
                        });
                    }else {
                        this.postRequest("/attendance/managementofmonth/calcbyworkid?monthday=" + this.statisticsValue.monthday,this.selectedEmps).then(resp => {
                            if (resp) {
                                this.initAtteMon();
                                this.statisticsDialog = false;
                            }
                        });
                    }
                }
                this.activeItemIndex++;
            },
            emptyStatistics(){
                this.statisticsValue.monthday='';
                this.way='0';
                this.selectedEmps=[];
                this.inputStatisticsDepName = '';
            },
            statistics(){
                this.emptyStatistics();
                this.statisticsDialog=true;
                this.activeItemIndex=0;
                if(!window.sessionStorage.getItem("emps")){
                    this.getRequest("/organization/management/employee/").then(resp => {
                        if (resp) {
                            this.employees = resp;
                            window.sessionStorage.setItem("emps", JSON.stringify(resp));
                        }
                    })
                } else {
                    this.employees = JSON.parse(window.sessionStorage.getItem("emps"));
                }
            },
            sizeChange(currentSize) {
                this.size = currentSize;
                this.initAtteMon();
            },
            currentChange(currentPage) {
                this.page = currentPage;
                this.initAtteMon();
            },
            searvhViewHandleNodeClick(data) {
                this.inputDepName = data.depname;
                this.searchValue.depid = data.depid;
                this.searchDepView = !this.searchDepView;
            },
            statisticsViewHandleNodeClick(data){
                this.inputStatisticsDepName = data.depname;
                this.statisticsValue.depid=data.depid;
                this.statisticsDepView = !this.statisticsDepView;
            },
            showSearchDepView() {
                this.searchDepView = !this.searchDepView;
            },
            showStatisticsDepView(){
                this.statisticsDepView = !this.statisticsDepView;
            },
            handleSelectionChange(val) {
                this.multipleSelection = val;
            },
            exportData(){
                this.postRequestExcel("/attendance/managementofmonth/export/",this.multipleSelection).then(resp => {
                    if (resp) {
                        const content = resp;
                        const blob = new Blob([content],{type: 'application/vnd.ms-excel'});
                        const fileName = this.moment().format('YYYY.MM.DD_HH-mm-ss')+'.xls';
                        if ('download' in document.createElement('a')) { // IE
                            const elink = document.createElement('a');
                            elink.download = fileName;
                            elink.style.display = 'none';
                            elink.href = URL.createObjectURL(blob);
                            document.body.appendChild(elink);
                            elink.click()
                            URL.revokeObjectURL(elink.href); // URL 
                            document.body.removeChild(elink);
                        } else { // IE10+
                            navigator.msSaveBlob(blob, fileName);
                        }
                    }
                });
            },
        }
    }
</script>

<style scoped>
    .top-menu{
        display: flex;
        justify-content: space-between;
    }
    .select-dep{
        width: 130px;
        display: inline-flex;
        font-size: 13px;
        border: 1px solid #dedede;
        height: 26px;
        border-radius: 5px;
        cursor: pointer;
        align-items: center;
        padding-left: 8px;
        box-sizing: border-box;
        margin: 0 10px 0 0;
    }
    .search-input{
        width: 320px;
        margin: 0 10px 0 10px;
    }
    .el-button--statistics{
        color: white;
        background: #13ce66;
    }
    /deep/ .findAllEmp-autocomplete li{
        line-height: normal;
        height: 50px;
        padding: 7px;
    }
    /deep/ .findAllEmp-autocomplete li .empname {
        text-overflow: ellipsis;
        overflow: hidden;
    }
    /deep/ .findAllEmp-autocomplete li .workid {
        font-size: 12px;
        color: #b4b4b4;
    }
    /deep/ .findAllEmp-autocomplete li.highlighted .workid{
        color: #ddd;
    }
</style>
